@font-face {
  font-family: RobotoBlack;
  src: url("Fonts/Roboto/Roboto-Black.ttf");
}



@font-face {
  font-family: Quicksand;
  src: url("Fonts/Quicksand/Quicksand-VariableFont_wght.ttf");
}

@font-face {
  font-family: Anton;
  src: url("Fonts/Anton/Anton-Regular.ttf");
}

