* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  font-family: Quicksand;
}

:root {
  --orange: #F58840;
  --halloween: #B85252;
  --black: #000000;
  --gray: #EADEDE;
  --shadow: #00000099;
}

span{
  color: var(--orange);
}


.logoBox, .logoBoxHome{
  width: 200px;
}

.backGroundOne{
  display: flex;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.backGroundTwo{
  display: none;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  background: #000000b1;
}

.animate {
  animation-name: ease;
  animation-duration: 3s;
}

@keyframes ease {
  0% {
    opacity: 0;
    transition: all ease-in-out 1s;
  }

  70% {
    opacity: 1;
    transition: all ease-in-out 1s;
  }
}

.homeImg {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.streamBox {
  width: 500px;
  height: 300px;
}

.streamBox iframe {
  width: 100%;
  height: 100%;
}

.videoBack {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.dark{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  background: #000000e5;
}


@keyframes scale {
  0% {
    scale: 1.0;
  }

  10% {
    scale: 1.2;
  }

  20% {
    scale: 1.0;
  }

  50% {
    scale: 1.5;
  }
}

header h3 {
  font-size: 80px;
  color: var(--orange)
}

h1 {
  font-size: 40px;
  color: var(--gray);
  text-align: center;
}

p {
  font-size: 20px;
  text-align: center;
  color: var(--gray);
}

a{
  color: var(--gray);
}

h3 {
  font-size: 27px;
}

span {
  font-size: 18px;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}


button {
  padding: 15px 30px;
  background: var(--halloween);
  color: var(--gray);
  /*margin: 10px;*/
  border: none;
  border-radius: 16px;
  outline: none;
  font-size: 15px;
  font-weight: bold;
  margin: 10px;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}


.btn {
  padding: 8px 15px;
  background: var(--black);
  color: var(--orange);
  /*margin: 10px;*/
  border: 0.5px solid var(--orange);
  border-radius: 16px;
  outline: none;
  font-size: 13px;
  display: flex;
  justify-content: center;
  align-items: center;
}



.toggled_modal_box {
  width: 90%;
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  position: absolute;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 10px;
  box-shadow: 10px 14px 20px rgba(0, 0, 0, 0)
}

#modal_box {
  width: 90%;
  height: 300px;
  background: #ffffff;
  border-radius: 15px;
  margin: 30px 10px;
  padding: 10px;
  box-shadow: 1px 2px 4px rgba(0, 0, 0, 0)
}

.movie_box {
  width: 100%;
  height: 200px;
}

iframe {
  width: 300px;
  height: 200px;
}

.movieBox {
  width: 150px;
  height: 250px;
  border-radius: 15px;
  background: var(--black);
  position: relative;
  border-radius: 12px;
  overflow: hidden;
  margin: 10px;
}

.movieBox img {
  /*filter: blur(0.5px);*/
}



.movieText {
  width: 100%;
  height: 50px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: url("https://favorbliss910.github.io/db/Images/one.png");
  background-size: contain;
  background-position: center;
  position: relative;
}



.mImg {
  width: 100%;
  height: 180px;
}



.movieText p {
  color: var(--gray);
  font-weight: bold;
  font-size: 9px;
  padding: 10px;
  text-align: center;
}

.movieText h3 {
  color: var(--gray);
  font-weight: bold;
  text-align: center;
  font-size: 16px;
  text-shadow: 1px 2px 3px var(--black);
  color: var(--gray);
  outline: 1px solid var(--orange);
  background: #00000099;
  position: absolute;
  top: 100px;
}

.movieList {
  width: 80%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin: 30px 10%;
}


.spaceUp {
  margin: 100px 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.showcase {
  width: 100%;
  height: 100vh;
  position: fixed;
}

.showImage {
  width: 100%;
  height: 100vh;
}

.showText {
  width: 100%;
  height: 100%;
  position: absolute;
  background: var(--shadow);
  margin: 0px 0%;
  padding: 0 0%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.inputBox {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 100px 0px;
}

.sImg {
  width: 80px;
  height: 130px;
}

input {
  width: 60%;
  height: 50px;
  background: var(--gray);
  color: var(--black);
  border-radius: 20px;
  outline: none;
  border: none;
  padding: 2px 30px;
  font-size: 30px;
}

.searchBox {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
}


@media only screen and (min-width: 421px) and (max-width: 750px) {
  .streamBox {
    width: 100%;
    height: 270px;
  }

  .backGroundOne{
    display: flex;
  }

  .backGroundTwo{
    display: none;
  }



}


@media only screen and (min-width: 250px) and (max-width: 420px) {
  .streamBox {
    width: 100%;
    height: 200px;
  }

  .logoBox{
    width: 100px;
  }

  .backGroundOne{
    display: none;
  }

  .backGroundTwo{
    display: flex;
  }

  input {
    width: 80%;
    height: 40px;
    margin: 10px 10%;
    background: var(--gray);
    color: var(--black);
    border-radius: 20px;
    outline: none;
    border: none;
    padding: 2px 12px;
    font-size: 20px;
  }
}